@keyframes fadeInSlideUp {
  from {
    opacity: 0;
    transform: translateY(20%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeOutSlideDown {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(20%);
  }
}

.modal-overlay {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0,0.4);
}

.modal-content {
  text-align: justify;
  background-color: #1c2046;
  margin: 10% auto;
  padding: 2vh;
  border: 1px solid white;
  width: 40%;
}

.modal-content.active {
  animation: fadeInSlideUp 0.5s ease-in-out;
}

.modal-content.hidden {
  display: none;
}

.note-form {
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.note-label {
  font-weight: bold;
}

.note-title,
.note-content,
.note-categories {
  width: 80%;
}

.submit-button,
.cancel-button {
  margin-right: 1%;
  border: 0;
  border-radius: 5px;
  width: 20%;
  height: 100%;
}

.submit-button {
  background-color: #3498DB;
}

.cancel-button {
  background-color: #F39C12;
}

.category-grid {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  gap: 2vh;
  max-width: 100%;
}

.category-card {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #383c6e;
  padding: 1vh;
  padding-left: 3vh;
  border-radius: 4px;
  max-width: fit-content;
  white-space: normal;
  position: relative;
  cursor: default;
}

.close-icon {
  position: absolute;
  left: 1vh;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

.close-icon:hover {
  background-color: lightslategrey;
  transition: background-color 0.3s ease;
}

